'use client'

import Link from 'next/link'
import Logo from './logo'
import { useEffect, useState } from 'react'
import { getUserFromToken } from '@/utils/jwt'
import { useRouter } from 'next/navigation'

export default function Header() {
  const [username, setUsername] = useState<string | null>(null)
  const router = useRouter()

  useEffect(() => {
    const user = getUserFromToken()
    if (user && user.username) {
      setUsername(user.username)
    }
  }, [])

  const handleLogout = () => {
    localStorage.removeItem('access')
    localStorage.removeItem('refresh')
    setUsername(null)
    router.push('/')
    router.refresh()  // force rechargement du layout avec le bon Ã©tat
  }

  return (
    <header className="z-30 mt-2 w-full md:mt-5">
      <div className="mx-auto max-w-6xl px-4 sm:px-6">
        <div className="relative flex h-14 items-center justify-between gap-3 rounded-2xl bg-gray-900/90 px-3">
          {/* Logo */}
          <div className="flex flex-1 items-center">
            <Logo />
          </div>

          {/* Username + Boutons */}
          <div className="flex items-center gap-4">
            {username ? (
              <>
                <p className="text-white font-medium">Hi {username} ðŸ‘‹</p>
                <button
                  onClick={handleLogout}
                  className="btn-sm bg-red-600 text-white px-3 py-1 rounded-md"
                >
                  Logout
                </button>
              </>
            ) : (
              <>
                <Link href="/login" className="btn-sm text-gray-300">
                  Login
                </Link>
                <Link
                  href="/register"
                  className="btn-sm text-white bg-indigo-600"
                >
                  Register
                </Link>
              </>
            )}
          </div>
        </div>
      </div>
    </header>
  )
}
